<template>
  <div v-if="store.state.user">
    <div class="school-badge">
      <div class="badge-image">
        <img :src="getAbsoluteUrl('img/logo.png')" alt=""/>
        <img :src="getAbsoluteUrl('img/school-badge.png')" alt=""/>
      </div>
    </div>
    <div class="display-grid">
      <div class="home-title">湖南省第二届大学生化学化工虚拟仿真竞赛</div>
      <ul class="home-list">
        <li @click="Goto('/purpose')">实验目的</li>
        <li @click="Goto('/principle')">实验原理</li>
        <li @click="Goto('/problem')">实验习题</li>
        <li v-if="store.state.showEXP" @click="Goto('/simulation')">仿真实验</li>
        <li v-if="store.state.showData" @click="Goto('/processing')">数据处理</li>
        <li v-if="store.state.showResult" @click="Goto('/result')">查看结果</li>
      </ul>
    </div>
  </div>
  <div v-else class="container">
    <div class="login-wrapper">
      <div class="header">登录</div>
      <div class="form-wrapper">
        <input :value="user.name" class="input-item" name="user-name" placeholder="姓名" type="text" @change="user.name = $event.target.value">
        <input :value="user.id" class="input-item" name="student-id" placeholder="学号" type="text" @change="user.id = $event.target.value">
        <div class="btn" @click="login">进入</div>
      </div>
    </div>
  </div>
</template>

<script setup>
//首页
import { getAbsoluteUrl } from '@/assets/js/api';
import store from '@/store';
import { ref } from 'vue';
import { useRouter } from 'vue-router';

const router = useRouter();
const user = ref({ name: '', id: '' });

function Goto(path) {
    router.push(path);
}

function login() {
    store.commit('setUser', Object.assign({}, user.value));
}
</script>

<style>
.container {
    height: 100vh;
}

.login-wrapper {
    background-color: #fff;
    width: 358px;
    border-radius: 15px;
    padding: 0 50px 40px;
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.header {
    font-size: 38px;
    font-weight: bold;
    text-align: center;
    line-height: 100px;
}

.input-item {
    display: block;
    width: 100%;
    margin-bottom: 20px;
    border: 0;
    padding: 10px;
    border-bottom: 1px solid rgb(128, 125, 125);
    font-size: 15px;
    outline: none;
}

.input-item::placeholder {
    text-transform: uppercase;
}

.btn {
    text-align: center;
    padding: 10px;
    width: 100%;
    margin-top: 40px;
    background-image: linear-gradient(to right, #a6c1ee, #fbc2eb);
    color: #fff;
}


a {
    text-decoration-line: none;
    color: #abc1ee;
}
</style>